@-webkit-keyframes cloud {
  0% {
    left: 14%;
    top: 200px;
    opacity: 0;
  }
  20% {
    left: 44%;
    top: 100px;
    opacity: 1;
  }
  40% {
    left: 65%;
    top: 50px;
    opacity: 1;
  }
  60% {
    left: 80%;
    top: 130px;
    opacity: 1;
  }
  80% {
    left: 90%;
    top: 180px;
    opacity: 0.5;
  }
  100% {
    left: 100%;
    top: 230px;
    opacity: 0;
  }
}

@keyframes cloud {
  0% {
    left: 14%;
    top: 200px;
    opacity: 0;
  }
  20% {
    left: 44%;
    top: 100px;
    opacity: 1;
  }
